.section-contacts {
   &__content {
      padding-top: 3.57rem;
      padding-bottom: 3.93rem;
      background-color: #fcf9f1; }

   &__inner {
      max-width: 620px;
      margin-left: 0; }

   &__row {
      display: flex;
      justify-content: space-between;
      align-items: flex-end; }

   &__socials {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      &-title {
         @include medium;
         @include lh;
         font-size: 22px;
         color: #000; }

      &-subtitle {
         margin-top: 2rem; }

      &-images {
         margin-top: 1.29rem;
         max-width: 160px;
         display: flex;
         justify-content: space-between; }

      &-link {
         display: flex;
         justify-content: center;
         align-items: center;
         width: 40px;
         height: 40px;
         padding: 14px 10px 14px 9px;
         border: 1.0px solid #e28234;
         border-radius: 50%;
         transition-duration: 0.1s;

         &:not(:first-child) {
            margin-left: 20px; }

         &:hover {
            margin-top: -7px;
            background-color: #fff; } }

      &-image {
         &--facebook {
            margin-right: -1px; }

         &--instagram {
            margin-top: -1px;
            margin-right: -2px; } } }

   &__phone {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      &-description {
         margin-top: 13px;
         @include lightItalic;
         @include lh;
         font-size: 12px;
         color: #000; }

      &-number {
         margin-top: 7px;
         @include bold;
         @include lh;
         font-size: 28px;
         color: #000;
         text-decoration: none;
         transition-duration: 0.7s;

         &:hover {
            color: red; } } }

   &__description {
      margin-top: 2.07rem;
      display: flex;
      flex-direction: column;
      align-items: flex-start; }

   &__items {
      width: 900px;
      margin-top: 17px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap; }

   &__item {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      &:not(:last-child) {
         margin-right: 10px;
         margin-bottom: 40px; }

      &-image {
         max-width: 200px;
         max-height: 80px; }

      &-paragraph {
         @include regular;
         @include lh;
         font-size: 11px;
         color: #000;
         margin-top: 15px; } } }

#map-1, #map-2, #map-3 {
   width: 400px;
   height: 240px; }


@media(max-width: 1440px) {
   .section-contacts {
      &__phone {
         &-number {
            font-size: 24px; } } } }

/*  1200  */
@media(max-width: $xl) {
   .section-contacts {
      &__phone {
         &-number {
            font-size: 22px; } }

      &__items {
         width: 800px; } }

   #map-1, #map-2, #map-3 {
      width: 380px;
      height: 230px; } }


/*  992  */
@media(max-width: $lg) {
   .section-contacts {
      &__phone {
         &-number {
            font-size: 20px; } }

      &__socials {
         &-link {
            width: 36px;
            height: 36px; } }

      &__items {
         width: 700px;
         justify-content: center; } }

   .contacts-subtitle {
      font-size: 14px; }



   #map-1, #map-2, #map-3 {
      width: 360px;
      height: 220px; } }

/*  768  */
@media(max-width: $md) {
   .section-contacts {
      &__phone {
         &-number {
            font-size: 18px; } }

      &__socials {
         &-images {
            margin-top: 15px; }

         &-link {
            width: 40px;
            height: 40px; } }

      &__description {
         margin-top: 25px; }

      &__items {
         flex-wrap: wrap;
         width: 600px;
         margin: 0 auto; }

      &__item {
         flex-basis: 40%;
         margin-bottom: 20px; } }


   #map-1, #map-2, #map-3 {
      width: 340px;
      height: 210px; } }

/*  660  */
@media(max-width: 660px) {
   .section-contacts {
      &__page {
         display: flex;
         flex-direction: column; }

      &__row {
         align-items: center; }


      &__description {
         margin-top: 30px; }

      &__items {
         width: 400px; } } }


/*  576  */
@media(max-width: $sm) {
   .section-contacts {
      &__content {
         padding-top: 6rem;
         padding-bottom: 6.2rem; }

      &__row {
         flex-direction: column;
         align-items: center; }

      &__phone {
         margin: 0 auto;
         order: -1;
         flex-direction: row;
         flex-wrap: wrap;
         align-items: center;
         justify-content: center;

         &-description {
            order: 1; }

         &-number {
            margin-top: -2px;
            margin-left: 14px; } }


      &__socials {
         margin-top: 30px;
         align-items: center; }

      &__items {
         justify-content: center;
         margin-top: 0; }

      &__item {
         margin-top: 20px; }

      &__items {
         width: 300px; } }

   #map-1, #map-2, #map-3 {
      width: 300px;
      height: 200px; } }

/*  320  */
@media(max-width: $ms) {
   .section-contacts {
      &__inner {
         max-width: 286px; }

      &__description {
         align-items: center; }

      &__items {
         margin-right: -10px; } }

   #map-1, #map-2, #map-3 {
      width: 280px;
      height: 200px; } }
