.main-reason {
   &__content {
      padding-top: 5.71rem;
      padding-bottom: 8.07rem;
      background-color: $bgc; }

   &__inner {
      max-width: 856px;
      margin: 0 auto; }

   &__column {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center; }

   &__items {
      margin-top: 1rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap; }

   &__item {
      margin-top: 4.07rem;
      display: flex;
      flex-direction: column;
      align-items: center;

      &-image {
         max-width: 120px;
         max-height: 120px;

         & img {
            max-width: 100%;
            max-height: 100%; } }

      &-description {
         margin-top: 2.71rem;
         max-width: 260px;
         min-width: 174px;
         text-align: center; }

      &-title {
         margin-bottom: 1.36rem; } } }

@media(max-width: 1440px) {
   .main-reason {
      &__inner {
         max-width: 800px; } } }

/*  1200  */
@media(max-width: $xl) {
   .main-reason {
      &__inner {
         max-width: 740px; }

      &__title {
         margin-left: -30px; } } }


/*  992  */
@media(max-width: $lg) {
   .main-reason {
      &__inner {
         max-width: 674px; }

      &__item {
         flex-basis: 50%; } } }


/*  768  */
@media(max-width: $md) {
   .main-reason {
      &__inner {
         max-width: 560px; }

      &__items {
         justify-content: space-around; }

      &__item {
         &-image img {
            max-width: 100px;
            max-height: 100px; } } } }

/*  576  */
@media(max-width: $sm) {
   .main-reason {
      &__inner {
         max-width: 360px; }

      &__title {
         margin-left: 0;
         text-align: center;
         min-width: 380px; } } }

/*  460  */
@media(max-width: $ml) {
   .main-reason {
      &__inner {
         max-width: 300px; }

      &__title {
         min-width: 280px;
         text-align: center; } } }

/*  320  */
@media(max-width: $ms) {
   .main-reason {
      &__content {
         max-width: 320px; }

      &__inner {
         max-width: 280px; } } }
