.section-center {
   &__content {
      padding-bottom: 5.71rem; }

   &__inner {
      max-width: 1000px;
      margin: 0 auto; }

   &__column {
      margin-top: 1.6rem;
      flex: 0 0 47.5%; }

   &__page {
      padding-top: 2.2rem;
      padding-left: 2.14rem;
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;

      &-title {
         font-size: 30px;
         @include lh;
         @include medium;
         color: #000; }

      &-subtitle {
         @include light;
         @include lh;
         margin-top: 0.64rem;
         font-size: 14px;
         color: #4b4b4b; }

      &-description {
         margin-top: 1.43rem;
         @include light;
         @include lh;
         font-size: 14px;
         color: #000;

         &:first-child {
            margin-top: 2.5rem; } }

      &-bg {
         background-color: $bgc;
         width: 72.5%;
         height: 18.85rem;
         position: absolute;
         top: 0;
         left: 0;
         z-index: -1; }

      &-images {
         flex: 0 0 48%;
         display: flex;
         flex-wrap: wrap;

         &-row {
            display: flex;
            justify-content: space-between; } }

      &-image {
         max-width: 139px;
         max-height: 139px;
         margin: 0 1.5rem 1.5rem 0;

         &--last {
            margin: 0; }

         &  img {
            max-width: 100%;
            max-height: 100%; } }

      &-list {
         &-item {
            list-style-image: url('../img/list-icons/list-logo-icon.svg');
            @include lh;
            @include light;
            font-size: 12px;
            color: #454545;
            margin-top: 1.2rem;


            // &:not(:first-child)
 } } }            //    margin-top: 1rem

   &__list {
      margin-top: 3.14rem;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-left: 20px; } }


/*  1440  */
@media (max-width: 1440px) {
   .section-center {
      &__inner {
         max-width: 980px; }

      &__page {
         &-title {
            font-size: 28px; }

         &-images {
            flex: 0 0 50%; }

         &-image {
            max-width: 126px;
            max-height: 126px;
            margin-bottom: 20px; } } } }

/*  1200  */
@media (max-width: $xl) {
   .section-center {
      &__inner {
         max-width: 900px; }

      &__page {
         &-title {
            font-size: 26px; }

         &-image {
            max-width: 120px;
            max-height: 120px; } } } }

/*  992  */
@media (max-width: $lg) {
   .section-center {
      &__inner {
         max-width: 800px; }

      &__page {
         &-title {
            font-size: 24px; }

         &-list:last-child {
            margin-left: 20px; } } } }



/*  768  */
@media (max-width: $md) {
   .section-center {
      &__inner {
         max-width: 580px; }

      &__page {
         flex-wrap: wrap;

         &-bg {
            min-width: 580px;
            min-height: 600px; }

         &-images {
            align-self: flex-start;
            margin-left: -10px;
            margin-top: 30px;
            flex: 1 0 90%; }

         &-image {
            margin-right: 0;
            margin-left: 20px;

            &:nth-child(3n) {
               display: none; } }

         &-title {
            font-size: 22px;
            text-align: center; }

         &-subtitle {
            text-align: center;
            font-size: 13px; }

         &-description {
            font-size: 14px; }

         &-list:last-child {
            margin-left: 20px; } }

      &__column {
         flex: 1 0 90%;
         order: -1; }

      &__description {
         margin-top: 25px; } } }


/*  660  */
@media (max-width: 660px) {
   .section-center {
      &__inner {
         max-width: 530px; }

      &__page {
         &-bg {
            min-width: 530px;
            min-height: 600px; }

         &-description {
            margin-top: 25px; }

         &-images {
            min-width: 100%;
            margin-left: 17px; }

         &-image {
            margin-right: 0;
            margin-left: 30px;

            &:nth-child(4n) {
               display: none; } } }

      &__list {
         display: none; } } }

/*  576  */
@media (max-width: $sm) {
   .section-center {
      &__inner {
         max-width: 430px; }

      &__page {
         &-bg {
            min-width: 440px;
            min-height: 600px; }

         &-images {
            margin-left: 0; }

         &-image {
               margin-right: 0;
               margin-left: 25px;

               & img {
                  max-width: 110px;
                  max-height: 110px; } } } } }

/*  460  */
@media (max-width: $ml) {
   .section-center {
      &__inner {
         max-width: 360px; }

      &__page {
         &-bg {
            min-width: 370px;
            min-height: 640px;
            margin-left: -5px; }

         &-image {
            margin-bottom: 30px;
            margin-left: 40px;

            &:nth-child(even) {
               display: none; } } } } }

/* 375 */
@media (max-width: $mm) {
   .section-center {
      &__inner {
         max-width: 310px; }

      &__page {
         &-bg {
            min-width: 320px;
            min-height: 520px;
            margin: 0; }

         &-images {
            display: none; } } } }

/* 320 */
@media (max-width: $ms) {
   .section-center {
      &__inner {
         max-width: 290px; }

      &__page {
         &-bg {
            min-width: 290px;
            margin: 0; } } } }
