@mixin light {
	font-family: 'Muller';
	font-weight: 300;
	font-style: normal; }

@mixin lightItalic {
	font-family: 'Muller';
	font-weight: 300;
	font-style: italic; }

@mixin regular {
	font-family: 'Muller';
	font-weight: normal; }

@mixin regularItalic {
	font-family: 'Muller';
	font-weight: normal;
	font-style: italic; }

@mixin medium {
	font-family: 'Muller';
	font-weight: 500; }

@mixin bold {
	font-family: 'Muller';
	font-weight: 900; }

@mixin lh {
	line-height: 1.2; }
