@import '_normalize.sass'
@import '_mixin.sass'
@import '_variable.sass'

html,body 
	height: 100%
	margin: 0
	padding: 0
	font-family: 'Muller'
	font-weight: 300
	font-size: 14px

*, *:after, *:before 
	box-sizing: border-box
	margin: 0
	padding: 0
	
.container
	max-width: 940px
	margin: 0 auto

.bg 
	width: 100%
	min-height: 100%
	position: absolute
	top: 0
	left: 0
	z-index: -1
	object-fit: cover

.column
	display: flex
	flex-direction: column

.section-title
	@include medium
	font-size: 36px
	@include lh
	color: #000
	text-align: center 

	&:after
		width: 118px
		height: 6px
		content: ''
		display: block
		margin: 0 auto
		margin-top: 19px
		background-image: url('../img/wavy-line.svg')

.description-text
	@include light
	@include lh 
	font-size: 18px
	color: #000

.section-subtitle
	@include medium
	@include lh
	font-size: 24px
	color: #000 

.circle
	display: inline-block
	width: 4px
	height: 4px
	background-color: #fff
	border-radius: 50%

.arrow
	width: 16px
	height: 25px
	cursor: pointer
	margin-top: 8%
	&-line
		width: 2px
		height: 16px
		display: inline-block
		background-color: #b8b8b8

		&:first-child
			transform: rotate(45deg)
		&:last-child
			margin-bottom: -10px
			margin-left: -4px
			transform: rotate(135deg)

	&-next
		transform: rotate(180deg)

.slide-circle 
	display: inline-block
	width: 6px
	height: 6px
	background-color: $lightGray 
	border-radius: 50%
	cursor: pointer 

	&--active
		display: inline-block
		width: 10px
		height: 10px
		border: 2px solid $lightGray
		border-radius: 50%

.desc-title
	@include bold
	font-size: 18px 
	@include lh  
	color: #000

.desc-paragraph
	@include light 
	@include lh 
	font-size: 16px 
	color: #000 

.desc-text
	@include regularItalic
	@include lh 
	font-size: 18px 
	color: #3f3f3f 

.step-arrow 
  width: 25px
  height: 2px
  background-color: #f0f0f0
  display: inline-block
  position: relative


.step-arrow:before, .step-arrow:after 
  content: ''
  display: inline-block
  width: 2px
  height: 12px
  background-color: #f0f0f0
  position: absolute
  right: 3px


.step-arrow:before 
  transform: rotate(-45deg)
  top: -9px


.step-arrow:after 
  transform: rotate(45deg)
  top: -1px

.section-subtitle 
	@include medium 
	line-height: 60px 
	font-size: 24px
	color: #000 

.contacts-subtitle
	@include lh 
	@include medium 
	font-size: 16px 
	color: #000

.show
	display: block

.close
	display: none 

.animationModal
	animation-name: appear
	animation-duration: 0.4s

@keyframes appear 
	0% 
		opacity: 0.3

	40% 
		opacity: 0.4

	70% 
		opacity: 0.7

	100%
		opacity: 1

.slow 
	animation-duration: 0.8s
	animation-name: fade

@keyframes fade 
	from
		opacity: 0.3

	to 
		opacity: 1
	

@media (max-width: 1440px) 
	html, body 
		font-size: 10px

	.container
		max-width: 840px

	.section-title
		font-size: 34px

		&:after 
			margin-top: 16px

	.section-subtitle
		font-size: 22px

	.desc-paragraph
		font-size: 14px


@media (max-width: $xl)
	html, body
		font-size: 9px

	.container
		max-width: 800px

	.section-title
		font-size: 30px

		&:after 
			margin-top: 12px

	.section-subtitle
		font-size: 20px

	.desc-title
		font-size: 16px 

	.desc-paragraph
		font-size: 14px  

	.desc-text
		font-size: 16px 

	.description-text
		font-size: 15px

	.arrow
		&-line 
			&:last-child 
				margin-left: -4px


/*	 992	*/
@media (max-width: $lg)
	html, body
		font-size: 7px

	.container
		max-width: 700px

	.section-title
		font-size: 28px

		&:after 
			margin-top: 8px

	.section-subtitle
		font-size: 19px

	.desc-title
		font-size: 15px 

	.desc-paragraph
		font-size: 13px  

	.desc-text
		font-size: 16px 

	.description-text
		font-size: 14px

	.logo > img
		max-width: 70px
		max-height: 90px


/*	 768	*/
@media (max-width: $md)
	html, body
		font-size: 5px

	.container
		max-width: 580px

	.section-title
		font-size: 24px

		&:after 
			margin-top: 5px

	.section-subtitle
		font-size: 17px

	.desc-title
		font-size: 14px 

	.desc-paragraph
		font-size: 12px  

	.desc-text
		font-size: 14px 

	.description-text
		font-size: 12px

	.logo > img
		max-width: 64px
		max-height: 80px

	.arrow 
		&-line:last-child
			margin-bottom: -11px



/*	 660	*/
@media (max-width: 660px)
	html, body
		font-size: 5px

	.container
		max-width: 500px


/*	 576	*/
@media (max-width: $sm)
	html, body
		font-size: 4px

	.container
		max-width: 380px

	.section-title
		font-size: 22px

	.section-subtitle
		font-size: 15px

	.desc-text
		font-size: 13px 

/*	 460	*/
@media (max-width: $ml)
	.container
		max-width: 330px 

	.section-subtitle
		font-size: 14px


/*	 375	*/
@media (max-width: $mm)
	.container
		max-width: 300px 

/*	 375	*/
@media (max-width: $mm)
	.logo > img
		max-width: 58px
		max-height: 70px



