.section-cause {
	&__content {
		padding-top: 5.71rem;
		padding-bottom: 5.5rem; }

	&__inner {
		max-width: 846px;
		margin: 0 auto; }

	&__title {
		margin: 0 auto;
		text-align: center; }

	&__items {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		margin-top: 5rem; }

	&__item {
		flex-basis: 28.37%;
		text-align: center;
		margin-top: 3.21rem;

		&-image {
			max-width: 170px;
			max-height: 170px;
			object-fit: cover; }

		&-description {
			@include regular;
			font-size: 16px;
			color: #000; } } }

@media (max-width: 1440px) {
	.section-cause {
		&__inner {
			max-width: 770px; }

		&__item {
			&-image {
				max-width: 160px;
				max-height: 160px; } } } }

/*	1200	*/
@media (max-width: $xl) {
	.section-cause {
		&__inner {
			max-width: 730px; }

		&__item {
			&-image {
				max-width: 140px;
				max-height: 140px; }

			&-description {
				font-size: 14px; } } } }


/*	992	*/
@media (max-width: $lg) {
	.section-cause {
		&__inner {
			max-width: 660px; }

		&__item {
			&-image {
				max-width: 130px;
				max-height: 130px; }

			&-description {
				font-size: 12px; } } } }

/*	 660	*/
@media (max-width: 660px) {
	.section-cause {
		&__item {
			flex: 0 1 50%;
			margin-top: 36px;

			&:last-child {
				margin: 20px auto 0 auto; } } } }

/*	 460	*/
@media (max-width: $ml) {
	.section-cause {
		&__item {
			&-image {
				max-width: 116px;
				max-height: 116px; } } } }

/*	375 */
@media (max-width: $mm) {
	.section-cause {
		&__items {
			flex-direction: column;
			align-items: center;
			justify-content: space-between; }

		&__item {
			max-width: 70%;

			&:not(:first-child) {
				margin-top: 24px; } } } }

/*	320 */
@media (max-width: $ms) {
	.section-cause {
		&__inner {
			max-width: 280px; }

		&__title {
			max-width: 260px;
			text-align: center; } } }
