.section-schedule {
	&__content {
		padding-top: 5.71rem;
		padding-bottom: 5.64rem; } }

.cards {
	margin: 3.57rem;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: wrap;
	margin-left: -20px; }


.card {
	width: 220px;
	height: 180px;
	background-color: #fff8e6;
	margin-top: 20px;
	margin-left: 20px;

	&__content {
		padding: 18px 20px 20px 20px; }

	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		text-align: center; }

	&__date {
		@include light;
		font-size: 14px;
		@include lh;
		color: #4b4b4b; }

	&__title {
		@include medium;
		@include lh;
		font-size: 18px;
		color: #000;
		margin-top: 13px; }

	&__more {
		@include regular;
		@include lh;
		font-size: 12px;
		color: #5a56af;
		margin-top: 19px;
		display: inline-block; }

	&__btn {
		min-width: 100%;
		min-height: 40px;
		padding: 10px 15px;
		background-color: #fff;
		border: 2px solid $orange;
		margin-top: 18px;

		/*text styles*/
		@include bold;
		font-size: 12px;
		text-transform: uppercase;
		@include lh;
		color: $orange;
		text-decoration: none;
		transition-property: background-color, color;
		transition-duration: 0.4s, 0.5s;

		&:hover {
			background-color: $orange;
			color: #fff; } } }

/*	1440	*/
@media (max-width: 1440px) {
	.cards {
		margin-left: 40px; } }

/*	1200	*/
@media (max-width: $xl) {
	.cards {
		margin-left: 32px; }

	.card {
		&__title {
			font-size: 16px; } } }

/*	992	*/
@media (max-width: $lg) {
	.cards {
		margin-left: 10px; }

	.card {
		width: 200px;
		height: 160px;

		&__btn {
			min-width: 90%;
			min-height: 30%;
			padding: 9px 12px; } } }

/*	768	*/
@media (max-width: $md) {
	.section-schedule .container {
		max-width: 680px; }

	.cards {
		margin-left: 0; } }

/*	660	*/
@media (max-width: 660px) {
	.cards {
		margin-left: 10px; } }

/*	576	*/
@media (max-width: $sm) {
	.section-schedule .container {
		max-width: 440px; }

	.cards {
		margin: 0; } }

/*	460	*/
@media (max-width: $ml) {
	.section-schedule {
		margin-top: 40px; }

	.section-schedule .container {
		max-width: 330px; }


	.cards {
		margin-top: 20px; }

	.card {
		margin: 20px auto 0 auto; } }

/*	375	*/
@media (max-width: $mm) {
	.section-schedule {
		margin-top: 0; } }

