.section-slider {
	&__content {
		position: relative; }

	&__column {
		padding-top: 5.71rem;
		background-color: $bgc;
		min-height: 487px; } }


.slider-container {
	padding-bottom: 3rem;
	max-width: 1920px;
	margin-top: -19.14rem;
	position: relative;
	overflow: hidden; }

.expectation-slides, .program-slides {
	display: flex;
	align-items: center;
	justify-content: space-between; }



.slide {
	max-height: 480px;

	& > .slide__shadow {
		display: none; }

	& > .slide__image {
		max-width: 100%;
		min-height: 480px;
		object-fit: none; } }


.swiper-slide-active {
	min-width: 940px;
	min-height: 530px;
	align-self: center;

	& > .slide__shadow {
		display: flex;
		width: 100%;
		height: 100%;
		background-color: rgba(68, 68, 68, 0.5);
		position: absolute;
		top: 0;
		left: 0;
		z-index: 100;
		padding: 29px;
		transition-duration: 1.5s;
		padding: 178px 200px 183px 200px;
		justify-content: center;
		align-items: center; }

	& .slide__description {
		max-width: 480px; }

	& > .slide__image {
		width: 100%;
		max-height: 100%;
		object-fit: fill; }

	&  .slide__title {
		@include medium;
		@include lh;
		font-size: 30px;
		text-transform: uppercase;
		color: #fff;
		text-align: center; }

	&  .slide__paragraph {
		margin-top: 24px;
		@include regular;
		@include lh;
		font-size: 18px;
		color: #fff;
		text-align: center; } }



@media (max-width: 1700px) {
	.slide {
		max-height: 440px;

		& > .slide__image {
			min-height: 440px; } }

	.swiper-slide-active {
		min-width: 840px;
		min-height: 490px; } }


@media (max-width: 1440px) {
	.slider-container {
		max-width: 1440px;
		margin-top: -21rem; }

	.slide {
		max-height: 390px;

		& > .slide__image {
