.section-reviews {
   &__content {
      padding-top: 5.71rem;
      padding-bottom: 5.71rem; }

   &__slider {
      margin-top: 5.71rem;
      display: flex;
      justify-content: space-between;

      &-column {
         flex: 0 0 80.42%;
         display: flex;
         flex-direction: column;
         align-items: center; }

      &-state {
         width: 49px;
         display: flex;
         justify-content: space-between;
         align-items: center;
         margin-top: 3.36rem; } }


   &__slide {
      display: none;
      justify-content: space-between;

      &--active {
         display: flex; }

      &-image {
         width: 150px;
         height: 150px; }

      &-avatar {
         width: 100%;
         height: 100%;
         border-radius: 50%;
         object-fit: cover; }


      &-description {
         flex: 0 0 75.4%;
         align-self: center;

         &-title {
            font-size: 18px;
            color: #000;
            margin-bottom: 5px;

            & span {
               @include regular;
               display: inline-block; }

            & h4 {
               display: inline-block;
               margin-left: 5px; } }

         &-link {
            font-size: 12px;
            @include lh;
            text-decoration: underline;
            color: #c94410; }

         &-paragraph {
            @include regularItalic;
            @include lh;
            font-size: 14px;
            color: #393939;
            margin-top: 1.21rem; } } } }


/*  1440  */
@media (max-width: 1440px) {
   .section-reviews {
      &__slide {
         &-image {
            margin-top: 20px;
            max-width: 136px;
            max-height: 136px; } } } }


@media (max-width: $xl) {
   .section-reviews {
      &__slide {
         &-description {

            &-title span {
               font-size: 16px; } } } } }


/*  992  */
@media (max-width: $lg) {
   .section-reviews {
      &__slide {
         &-image {
            max-width: 130px;
            max-height: 130px; }

         &-description {
            flex-basis: 70%;

            &-title span {
               font-size: 15px; } } } } }

/*  768  */
@media (max-width: $md) {
   .section-reviews {
      &__slider {
         margin: 80px 0 0 -50px;
         min-width: 640px;
         justify-content: space-around; }

      &__slide {
         &-image {
            max-width: 130px;
            max-height: 130px; }

         &-description {
            &-paragraph {
               font-size: 13px; } } } } }

/*  660  */
@media (max-width: 660px) {
   .section-reviews {
      &__slider {
         margin: 80px 0 0 -36px;
         min-width: 560px; }

      &__slide {
         &-image {
            max-width: 120px;
            max-height: 120px; } } } }


/*  576  */
@media (max-width: $sm) {
   .section-reviews {
      &__slider {
         min-width: 440px;
         align-items: center; }

      &__slide {
         flex-direction: column;

         &-image {
            max-width: 140px;
            max-height: 140px;
            align-self: center; }

         &-description {
            margin-top: 16px;

            &-title {
               text-align: center; }

            &-link {
               display: inline-block;
               width: 100%;
               text-align: center;
               font-size: 14px; }

            &-paragraph {
               margin-top: 15px;
               font-size: 12px;
               text-align: justify; } } } } }

/*  460  */
@media (max-width: $ml) {
   .section-reviews {
      &__slider {
         min-width: 350px;
         margin-left: -10px; } } }

/*  375  */
@media (max-width: $mm) {
   .section-reviews {
      &__slider {
         min-width: 310px;
         margin-left: -2px; } } }

/*  320  */
@media (max-width: $ms) {
   .section-reviews {
      &__slider {
         min-width: 300px; } } }
