.section-cost {
   &__content {
      padding-top: 5.71rem;
      padding-bottom: 5.36rem;
      background-color: $bgc; }

   &__inner {
      max-width: 820px;
      margin: 0 auto; }

   &__page {
      margin-top: 4rem; }

   &__features {
      padding: 3.5rem 3.64rem 3.14rem 3.5rem;
      display: flex;
      flex-direction: column;
      background-color: #fff; }

   &__feature {
      display: flex;
      justify-content: space-between;
      align-items: center;

      &-description {
         @include light;
         font-size: 21px;
         line-height: 48px;
         color: #000; }

      &-price {
         @include bold;
         font-size: 20px;
         line-height: 48px;
         color: #dd490f;

         & span {
            @include light; } } }


   &__preview {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: #f4e1b6;
      padding: 1.07rem 3.93rem 1.07rem 3.57rem;

      &-description {
         @include medium;
         @include lh;
         font-size: 20px;
         color: #000; }

      &-link {
         padding: 16px 49px 14px 43px;
         border: 2px solid #e28234;

         // text styles
         text-transform: uppercase;
         @include bold;
         color: #e28234;
         @include lh;
         font-size: 12px;
         text-decoration: none;
         transition-duration: 0.5s;

         &:hover {
            background-color: #fff;
            border: 2px solid #000; } } }

   &__inner-column {
      max-width: 734px;
      margin: 0 auto; }

   &__description {
      margin-top: 3.5rem;
      display: flex;
      flex-direction: column;

      &-subtitle {
         text-align: center; }

      &-list {
         margin-top: 2.71rem;

         &-item {
            list-style-image: url('../img/list-icons/check-mark.svg');
            @include regular;
            color: #4e4e4e;
            padding-left: 20px;

            &:not(:first-child) {
               margin-top: 1.3rem; }

            &--excluded {
               list-style-image: url('../img/list-icons/cross.svg'); } } } }

   &__text {
      margin-top: 48px;

      &-paragraph {
         @include regular;
         text-align: center; } } }

@media(max-width: 1440px) {
   .section-cost {
      &__feature {
         &-description {
            font-size: 19px; }

         &-price {
            font-size: 18px; } }

      &__text {
         margin-top: 40px; } } }

/*  1200  */
@media(max-width: $xl) {
   .section-cost {
      &__features {
         padding: 3rem 3.64rem 3.14rem 3rem; }

      &__feature {
         &-description {
            font-size: 17px; }

         &-price {
            font-size: 15px; } }

      &__preview {
         &-description {
            font-size: 18px; }

         &-link {
            padding: 13px 35px 12px 35px; } }

      &__description {
         &-list {
            padding: 0 40px; } } } }


/*  992  */
@media(max-width: $xl) {
   .section-cost {
      &__inner {
         max-width: 650px; }

      &__feature {
         &-description {
            font-size: 16px; }

         &-price {
            font-size: 14px; } }

      &__preview {
         &-description {
            font-size: 17px; }

         &-link {
            padding: 11px 35px 11px 35px; } } } }





/*  768  */
@media(max-width: $md) {
   .section-cost {
      &__inner {
         max-width: 560px; }

      &__feature {
         &-description {
            font-size: 14px; }

         &-price {
            font-size: 13px; } }

      &__preview {
         &-description {
            font-size: 15px; }

         &-link {
            padding: 11px 35px 11px 35px; } } } }

/*  660  */
@media(max-width: 660px) {
   .section-cost {
      &__page {
         margin-top: 34px; }

      &__description {
         &-list {
            &-item {
               padding-left: 6px; } } } } }

/*  576  */
@media(max-width: $sm) {
   .section-cost {
      &__inner {
         max-width: 440px; }

      &__page {
         margin-top: 34px; }

      &__feature {
         padding: 5px;
         align-items: flex-end;

         &-description {
            line-height: 20px;
            flex: 0 1 70%; } }

      &__preview {
         padding: 10px;

         &-description {
            font-size: 12px;
            flex: 0 1 65%; }

         &-link {
            padding: 7px 26px 7px 26px;
            font-size: 12px; } }

      &__description {
         &-list {
            &-item {
               &:not(:first-child) {
                  margin-top: 12px; } } } } } }

/*  460  */
@media(max-width: $ml) {
   .section-cost {
      &__inner {
         min-width: 350px;
         margin-left: -10px; } } }

/*  375  */
@media(max-width: $mm) {
   .section-cost {
      &__inner {
         min-width: 320px; }

      &__feature {
         align-items: center; } } }

/*  320  */
@media(max-width: $ms) {
   .section-cost {
      &__inner {
         min-width: 290px;
         margin-left: 2px; }

      &__feature {
         align-items: flex-end;
         &-description {
            font-size: 12px;
            flex: 1 0 73%; } } } }
