.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0,0,0,.5);

  &__dialog {
    max-width: 530px;
    min-height: 440px;
    margin: 50px auto; }

  &__content {
    position: relative;
    width: 100%;
    padding: 46px 88px 34px 88px;
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 22px;
    max-height: 80vh;
    overflow-y: hidden; }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center; }

  &__close {
    position: absolute;
    top: 14px;
    right: 20px;
    font-size: 40px;
    color: #000;
    opacity: .5;
    font-weight: 700;
    border: none;
    background-color: #fff;
    cursor: pointer; }

  &__title {
    font-family: 'Museo Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 26px;
    line-height: 35px;
    text-align: center;
    color: rgba(65, 65, 65, 1);
    margin-bottom: 45px; }

  &__inputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 350px; }

  &__input {
    border: none;
    border-radius: 8px;
    text-align: center;
    @include regular;
    padding: 16px 84px 17px 84px;

    &:not(:last-child) {
      margin-bottom: 23px; }

    &[placeholder] {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 21px;
      color: rgba(0, 0, 0, 0.75); } }


  &__name {
    background-color: #EDDDCD; }


  &__surname {
    background-color: #F0D09F; }

  &__phone-number {
    background-color: #B4F5CE; }

  &__btn {
    background: #E8C23C;
    border-radius: 22px;
    max-width: 280px;
    margin-top: 40px;
    padding: 16px 88px;
    border: none;

    @include bold;
    font-size: 16px;
    color: #fff;
    text-transform: uppercase;
    text-align: center; }

  &__message-block {
    margin-top: 20px;
    min-height: 50px;
    @include medium;
    font-size: 22px;
    text-align: center; } }


@media (max-width: 1440px) {
  .modal {
    &__dialog {
      max-width: 480px;
      min-height: 400px;
      margin-top: 200px; }

    &__content {
      padding: 30px 75px 26px 75px; }

    &__form {
      max-height: 420px; } } }

@media (max-height: 1366px) {
  .modal {
    &__dialog {
      margin-top: 150px; } } }


/*  576  */
@media (max-width: $sm) {
  .modal {
    &__dialog {
      max-width: 420px;
      min-height: 400px;
      margin-top: 150px; }

    &__title {
      max-width: 300px;
      font-size: 20px;
      line-height: 20px; }

    &__content {
      padding: 30px 75px 26px 75px; }

    &__form {
      max-height: 400px; }

    &__inputs {
      max-width: 280px; }

    &__input {
      padding: 16px 40px 14px 40px; }

    &__btn {
      max-width: 230px;
      padding: 14px 40px;
      margin-top: 30px; }

    &__message-block {
      font-size: 16px; } } }
