.section-instructors {
   &__content {
      padding-top: 5.64rem;
      background-color: $bgc; }

   &__column {
      display: flex;
      align-items: center; }

   &__pages {
      margin-top: 70px; } }


.instructors-slider {
   max-width: 940px;

   &:not(:first-child) {
      margin-top: 19px; } }

.instructor-page {
   position: relative;
   max-width: 940px;

   &__image {
      max-width: 940px;
      max-height: 540px; }

   &__text {
      position: absolute;
      bottom: 4px;
      left: 0;
      z-index: 100;
      width: 100%;
      background-color: rgba(99, 99, 99, 0.5);
      padding: 2.86rem 6.28rem 3.2rem 4.21rem;
      display: flex;
      justify-content: space-between;

      &-main {
         flex-basis: 32%; }

      &-line {
         width: 2px;
         height: 78px;
         background-color: #fff; }

      &-more {
        flex-basis: 57.9%; }

      &-circles {
         position: absolute;
         z-index: 10;
         right: 10px;
         bottom: 10px; } }

   &__title {
      @include bold;
      @include lh;
      font-size: 30px;
      color: #fff; }

   &__subtitle {
      @include regularItalic;
      font-size: 16px;
      color: #fff;

      &:first-child {
         margin-top: 1.86rem; } }

   &__text-paragraph {
      @include regular;
      @include lh;
      font-size: 14px;
      color: #fff; } }


.swiper-container-horizontal>.swiper-pagination-bullets {
   width: auto;
   left: 92%; }

.swiper-pagination-bullet {
   display: inline-block;
   width: 6px;
   height: 6px;
   background-color: $lightGray;
   border-radius: 50%;
   cursor: pointer;
   opacity: 1; }

.swiper-pagination-bullet-active {
   display: inline-block;
   width: 10px;
   height: 10px;
   border: 2px solid $lightGray;
   opacity: 1; }


@media (max-width: 1440px) {
   .instructors-slider {
      max-width: 860px; }

   .instructor-page {
      max-width: 860px;

      &__text {
         &-main {
            flex-basis: 36%; } }

      &__image {
         max-width: 860px;
         max-height: 500px; }

      &__text {
         padding: 2.4rem 6.28rem 2.4rem 4.21rem; } } }

@media (max-width: $xl) {
   .instructors-slider {
      max-width: 800px; }

   .instructor-page {
      max-width: 800px;

      &__image {
         width: 800px;
         max-height: 460px; }

      &__title {
         font-size: 24px; }

      &__text {
         padding: 2.4rem 5rem 2.4rem 4rem;

         &-main {
            flex-basis: 34%; } } }

   .swiper-container-horizontal>.swiper-pagination-bullets {
      left: 86%; } }

@media (max-width: $lg) {
   .instructors-slider {
      max-width: 700px; }

   .instructor-page {
      max-width: 700px;

      &__image {
         max-width: 700px;
         max-height: 400px; }

      &__title {
         font-size: 23px; } } }



@media (max-width: $md) {
   .instructors-slider {
      max-width: 540px; }

   .instructor-page {
      max-width: 540px;
      height: 340px;

      &__image {
         max-width: 540px;
         height: 340px; }

      &__title {
         font-size: 20px; }

      &__text {
         bottom: 0;
         max-width: 100%; }

      &__text-paragraph {
         font-size: 13px; }

      &__subtitle {
         font-size: 15px; } } }

@media (max-width: $sm) {
   .instructors-slider {
      max-width: 420px; }

   .instructor-page {
      max-width: 420px;
      height: 300px;

      &__image {
         max-width: 420px;
         height: 280px; }

      &__title {
         font-size: 18px;
         margin-bottom: 6px; }

      &__subtitle {
         display: none; }

      &__text {
         flex-direction: column;
         padding: 12px 5px;
         margin-bottom: 20px;
         width: 420px;

         &-main {
            flex: 1 0 100%; }

         &-line {
            display: none; } }

      &__text-paragraph {
         font-size: 12px; } }

   .swiper-container-horizontal>.swiper-pagination-bullets {
      left: 80%;
      bottom: 30px; }

   .swiper-pagination-bullet {
      width: 5px;
      height: 5px; }
   .swiper-pagination-bullet-active {
      width: 8px;
      height: 8px; } }


@media (max-width: $ml) {
   .instructors-slider {
      max-width: 340px; }

   .instructor-page {
      max-width: 340px;

      &__image {
         max-width: 340px;
         max-height: 240px; }

      &__text {
         width: 340px;
         padding-bottom: 5px;
         margin-bottom: 60px; } }

   .swiper-container-horizontal>.swiper-pagination-bullets {
      left: 76%;
      bottom: 70px; } }

@media (max-width: $mm) {
   .instructors-slider {
      max-width: 300px; }

   .instructor-page {
      max-width: 300px;

      &__image {
         max-width: 300px;
         height: 220px; }

      &__text {
         width: 300px;
         margin-bottom: 80px; }

      &__text-paragraph:last-child {
         display: none; } }


   .swiper-container-horizontal>.swiper-pagination-bullets {
      left: 76%;
      bottom: 90px; } }



@media (max-width: $ms) {
   .instructors-slider {
      max-width: 290px; }

   .instructor-page {
      max-width: 290px;

      &__image {
         max-width: 290px;
         height: 200px; }

      &__title {
         font-size: 16px; }

      &__text {
         width: 290px;
         margin-bottom: 100px; }

      &__text-paragraph {
         font-size: 11px; } }


   .swiper-container-horizontal>.swiper-pagination-bullets {
      left: 74%;
      bottom: 110px; } }
