.footer {
   &__content {
      padding-top: 12px;
      padding-bottom: 12px;
      background-color: #383838; }

   &__inner {
      max-width: 986px;
      margin: 0 auto; }

   &__row {
      display: flex;
      justify-content: space-between;
      align-items: center; }

   &__description {
      margin-left: 2.79rem;
      flex-basis: 90.87%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      &-text {
         flex-basis: 43.97%; } }

   &__more {
      min-height: 42px;
      display: flex;
      flex-direction: column;
      justify-content: space-between; }

   &__more-link {
      @include bold;
      font-size: 18px;
      color: #fff; } }


.footer-paragraph {
   @include regular;
   @include lh;
   font-size: 12px;
   color: #fff; }


/*  1440  */
@media (max-width: 1440) {
   .footer {
      &__inner {
         max-width: 910px; } } }

/*  1200  */
@media (max-width: $xl) {
   .footer {
      &__inner {
         max-width: 860px; } } }

/*  992  */
@media (max-width: $lg) {
   .footer {
      &__inner {
         max-width: 780px; }

      &__more {
         &-link {
            font-size: 16px; } } } }


/*  768  */
@media (max-width: $md) {
   .footer {
      &__inner {
         max-width: 560px; }

      &__more {
         &-link {
            font-size: 16px; } } } }

/*  660  */
@media (max-width: 660px) {
   .footer {
      &__inner {
         max-width: 500px; }

      &__description {
         text-align: center;
         justify-content: flex-end;

         &-text {
            display: none; } } } }

/*  576  */
@media (max-width: $sm) {
   .footer {
      &__inner {
         max-width: 380px; } } }

/*  460  */
@media (max-width: $ml) {
   .footer {
      &__inner {
         max-width: 320px; } } }

/*  320  */
@media (max-width: $ms) {
   .footer {
      &__inner {
         max-width: 290px; } } }
