.section-info {
   &__content {
      padding-top: 5.71rem;
      padding-bottom: 5.14rem; }

   &__inner {
      max-width: 920px;
      margin: 0 auto; }

   &__inner-column {
      max-width: 620px;
      margin: 0 auto; }

   &__description {
      margin-top: 5rem;

      &-text {
         text-align: center;
         &:not(:first-child) {
            margin-top: 2.14rem; } } }

   &__subtitle {
      margin-top: 4.5rem;
      text-align: center; } }

.info-items {
   max-width: 620px;
   margin-top: 4.43rem;
   display: flex;
   flex-direction: column;
   justify-content: space-between; }

.info-item {
   display: flex;
   justify-content: space-between;
   align-items: center;

   &:not(:first-child) {
       margin-top: 2.14rem; }

   &__image {
      max-width: 120px;
      max-height: 120px; }

   &__description-text {
      flex-basis: 74.19%;
      @include lh;
      @include light;
      font-size: 17px;
      color: #000;
      margin-left: 39px; } }


/*  992  */
@media (max-width: $lg) {
   .section-info {
      &__description {
         margin-top: 7rem; } }

   .info-item {
      &__description-text {
         font-size: 16px; } } }

/*  768  */
@media (max-width: $md) {
   .info-item {
      &:not(:first-child) {
         margin-top: 5rem; }

      &__image {
         max-width: 110px;
         max-height: 110px; }

      .info-item {
         &__description-text {
            font-size: 14px; } } } }

/*  576  */
@media (max-width: $sm) {
   .section-info {
      &__column {
         min-width: 380px; } }

   .info-item {
      flex-direction: column;
      align-items: center;

      &__image {
         max-width: 100px;
         max-height: 100px; }

      &__description-text {
         font-size: 13px;
         flex-basis: 100%;
         margin-left: 0;
         margin-top: 14px;
         text-align: center; } } }

/*  460  */
@media (max-width: $ml) {
   .section-info {
      &__inner {
         max-width: 300px; }

      &__column {
         min-width: 100%; }

      &__subtitle {
         text-align: center;

         & > h3 {
            line-height: 20px; } } }

   .info-item {
      &:not(:first-child) {
         margin-top: 7rem; }
      &__description-text {
         font-size: 12px; } } }


/*  375  */
@media (max-width: $ms) {
   .section-info {
      &__inner {
         max-width: 280px; }

      &__subtitle {
         max-width: 240px;
         text-align: center;
         margin: 24px auto 0 auto; } } }
