.section-work {
   &__content {
      padding-top: 5.71rem;
      padding-bottom: 5.71rem; }

   &__column {
      display: flex;
      flex-direction: column;
      align-items: center; }

   &__description {
      margin-top: 2.64rem; }

   &__items {
      margin-top: 4.29rem;
      display: flex;
      justify-content: space-between;
      align-items: center; }

   &__item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @include regular;
      @include lh;

      &-image {
         max-width: 90px;
         max-height: 90px; }

      &-description {
         text-align: center;
         margin-top: 2.64rem;
         max-width: 136px;
         min-width: 96px;
         font-size: 12px;
         color: #3f3f3f; }

      &-link {
         color: #ea7231;
         @include regular;
         @include lh;
         font-size: 12px; }

      &-step-arrow {
         margin-top: -50px; } } }

/*  1200  */
@media (max-width: $xl) {
   .section-work {
      &__description {
         margin-top: 4.5rem; }

      &__items {
         min-width: 900px; }

      &__item {
         &-image img {
            max-width: 80px;
            max-height: 80px; } } } }


/*  992  */
@media (max-width: $lg) {
   .section-work {
      &__description {
         margin-top: 5rem; }

      &__items {
         min-width: 800px;
         flex-wrap: wrap;
         justify-content: space-around;
         align-items: center; }

      &__item {
         flex-basis: 30%;
         margin-bottom: 30px;

         &-image img {
            max-width: 74px;
            max-height: 74px; } } }

   .step-arrow:nth-child(6) {
      display: none; } }


/*  768  */
@media (max-width: $md) {
   .section-work {
      &__items {
         min-width: 600px; } } }

/*  660  */
@media (max-width: 660px) {
   .section-work {
      &__items {
         min-width: 500px; } } }

/*  576  */
@media (max-width: $sm) {
   .section-work {
      &__items {
         min-width: 400px;
         flex-direction: column; }

      &__item {
         &:not(:last-child) {
            margin-top: 24px;
            flex-basis: 100%; }

         & img {
            min-width: 85px;
            min-height: 85px; } }

      .step-arrow {
         display: none; }

      &__description {
         text-align: center; } } }

/*  460  */
@media (max-width: $ml) {
   .section-work {
      &__items {
         min-width: 360px; } } }

/*  375  */
@media (max-width: $mm) {
   .section-work {
      &__items {
         min-width: 340px; } } }


/*  320  */
@media (max-width: $ms) {
   .section-work {
      &__items {
         min-width: 310px; } } }
