.section-header {
	&__content {
		// width: 100%
		min-height: 100vh;
		position: relative; }

	&__wrap {
		padding-top: 1.36rem;
		padding-bottom: 2.93rem; }

	&__column {
		margin-top: 12.79rem;
		display: flex;
		align-items: center; }

	&__about {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-start; }

	&__title {
		@include medium;
		line-height: 1.2;
		color: #fff;
		font-size: 60px;
		text-transform: uppercase; }

	&__subtitle {
		font-size: 20px;
		@include regular;
		text-transform: uppercase;
		color: #fff;
		@include lh; }

	&__description {
		@include regular;
		@include lh;
		font-size: 16px;
		color: #fff;
		margin-top: 1rem; }

	&__request-button {
		@include bold;
		@include lh;
		font-size 18px {}
		text-transform: uppercase;
		color: #fff;
		display: inline-block;
		min-width: 300px;
		max-height: 60px;
		padding: 22px 72px 22px 74px;
		text-align: center;
		border: 2px solid #fff;
		margin-top: 7.07rem;
		text-decoration: none;
		transition-duration: 1s;

		&:hover {
			color: #000;
			background-color: #fff; } }

	&__socials {
		min-width: 100px;
		margin-top: 10rem;
		display: flex;
		justify-content: space-between; }

	&__social-link {
		display: flex;
		justify-content: center;
		align-items: center;
		border: 1px solid #fff;
		cursor: pointer;
		border-radius: 50%;
		width: 26px;
		height: 26px;
		transition-duration: 1s;

		&:before {
			content: '';
			display: block;
			background-image: url('../img/social-icons/ellips.jpg');
			position: absolute;
			z-index: -1; }

		&--vk:hover {
			background-color: #0362fc; }

		&--facebook:hover {
			background-color: #0339fc; }

		&--instagram:hover {
			background-color: #e81558; } } }

.header {
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&__contacts {
		display: flex;
		flex-direction: column; }

	&__call-info {
		@include regular;
		font-size: 14px;
		color: #fff; }

	&__phone-number {
		display: inline-block;
		text-decoration: none;
		@include bold;
		font-size: 20px;
		line-height: 18px;
		color: #fff;
		animation-duration: 2.4s;

		&:hover {
			animation-name: flash; } } }


@keyframes flash {
	20% {
		opacity: 0.3; }
	50% {
		opacity: 0.7; }
	100% {
		opacity: 1; } }

/*	2560*/
@media (max-width: 2560px) {
	.section-header {
		&__column {
			margin-top: 10rem; } } }


@media (max-width: 1440px) {
	.section-header {
		&__column {
			margin-top: 11rem; }

		&__title {
			font-size: 52px; }

		&__subtitle {
			font-size: 18px; }

		&__request-button {
			font-size: 12px; }

		&__socials {
			margin-top: 8rem; } }

	.header {
		&__row {
			max-width: 740px;
			margin: 0 auto; } } }

/*	1366	*/
@media (max-width: 1366px) {
	.section-header {
		&__title {
			font-size: 48px; } } }


/*	1200	*/
@media (max-width: $xl) {
	.section-header {
		&__column {
			margin-top: 10rem; }

		&__title {
			font-size: 46px; }

		&__subtitle {
			font-size: 16px; }

		&__description {
			font-size: 14px; }

		&__request-button {
			min-width: 260px;
			margin-top: 6.5rem;
			padding: 20px 66px 20px 68px; } } }


/*	 1024	*/
@media screen and (max-width: 1024px) {
	.section-header {
		&__column {
			margin-top: 400px; }

		&__title {
			font-size: 44px; }

		&__subtitle {
			margin-top: 100px; }

		&__request-button {
			margin-top: 200px; }

		&__socials {
			margin-top: 220px; }

		&__social-link {
			width: 50px;
			height: 50px;

			&:not(:last-child) {
				margin-right: 24px; } } } }


/*	 992	*/
@media (max-width: $lg) {
	.section-header {
		&__column {
			margin-top: 300px; }

		&__title {
			font-size: 40px; }

		&__request-button {
			margin-top: 10rem;
			min-width: 230px;
			font-size: 12px;
			padding: 18px 40px 18px 40px; }

		&__socials {
			margin-top: 12.5rem; } }

	.header {
		&__row {
			max-width: 660px;
			margin: 0 auto; } } }

/*	 768	*/
@media (max-width: $md) {
	.section-header {
		&__column {
			margin-top: 320px; }

		&__title {
			font-size: 38px; }

		&__subtitle {
			margin-top: 0; }

		&__request-button {
			margin-top: 200px; } } }


/*	 576	*/
@media (max-width: $sm) {
	.section-header {
		&__column {
			margin-top: 65%; }

		&__title {
			font-size: 32px; }

		&__subtitle {
			font-size: 14px; }

		&__socials {
			margin-top: 18rem; }

		&__social-link {
			width: 42px;
			height: 42px;

			&:not(:last-child) {
				margin-bottom: 30px; } } }

	.header {
		&__phone {
			&-number {
				margin-top: 6px; } } } }


/*	460 */
@media (max-width: $ml) {
	.section-header {
		&__row {
		 	max-width: 300px; }

		&__column {
			margin-top: 60%; }

		&__title {
		   font-size: 30px;
		   text-align: center; }

		&__subtitle {
			text-align: center; }

		&__description {
			text-align: center; }

		&__request-button {
			margin-top: 90px; }

		&__social-link {
			width: 36px;
			height: 36px; } } }

/*  320  */
@media(max-width: $ms) {
	.section-header {
		&__column {
			margin-top: 48%; } }

	.header {
		&__row {
		 	max-width: 280px; } } }

