.section-about {
	&__content {
		background-color: $bgc;
		color: #000;
		@include light;
		padding-top: 3.57rem;
		padding-bottom: 3.57rem; }

	&__inner {
		max-width: 864px;
		margin: 0 auto; }

	&__row {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&__image {
		max-width: 100%;
		max-height: 100%; }

	&__description {
		flex-basis: 46%; }

	&__caption {
		@include medium;
		@include lh;
		color: #000;
		margin-bottom: 3.29rem; }

	&__subtitle {
		font-size: 30px; }

	&__title {
		font-size: 48px;
		text-transform: uppercase; }

	&__text {
		font-size: 16px;
		@include lh; }

	&__items {
		display: flex;
		flex-direction: column;
		margin-top: 2.86rem; }

	&__item {
		display: flex;

		&:not(:first-child) {
			margin-top: 20px; }

		&-description {
			font-size: 18px;
			text-transform: uppercase;
			margin-left: 21px; } } }



@media (max-width: 1440px) {
	.section-about {
		&__inner {
			max-width: 780px; }

		&__img {
			max-height: 450px; }

		&__title {
			font-size: 42px; }

		&__item {
			&-description {
				font-size: 17px; } } } }

/*	1200	*/
@media (max-width: $xl) {
	.section-about {
		&__inner {
			max-width: 740px; }

		&__img {
			max-height: 400px; }

		&__title {
			font-size: 36px; }

		&__subtitle {
			font-size: 26px; }

		&__text {
			font-size: 14px; }

		&__item {
			&-description {
				font-size: 16px; } } } }


/*	992	*/
@media (max-width: $lg) {
	.section-about {
		&__inner {
			max-width: 680px; }

		&__img {
			max-height: 360px; }

		&__title {
			font-size: 32px; }

		&__subtitle {
			font-size: 24px; }

		&__text {
			font-size: 14px; }

		&__item {
			&:not(:first-child) {
				margin-top: 18px; }

			&-description {
				font-size: 14px;
				margin-left: 16px; } } } }

/*	992	*/
@media (max-width: $lg) {
	.section-about {
		&__inner {
			max-width: 560px; }

		&__img {
			max-height: 330px; }

		&__caption {
			margin-top: 8px; }

		&__title {
			font-size: 28px;
			margin-top: 6px; }


		&__subtitle {
			font-size: 22px; } } }

/*	 660	*/
@media (max-width: 660px) {
	.section-about {
		&__img {
			display: none; }

		&__description {
			flex: 1 1 100%; }

		&__title, &__subtitle {
			display: inline-block; }

		&__title {
			font-size: 25px;
			margin-left: 12px; }

		&__items {
			flex-direction: row;
			align-items: center;
			margin-top: 26px; }

		&__item {
			flex: 1 1 33.33%;
			align-items: center;

			&:not(:first-child) {
				margin-top: 0; }

			&-description {
				font-size: 13px; } } } }

/*	 576	*/
@media (max-width: $sm) {
	.section-about {
		&__item {
			&-image {
				max-width: 23px;
				max-height: 24px; } } } }



/*	 375	*/
@media (max-width: $mm) {
	.section-about {
		&__items {
			flex-direction: column;
			align-items: flex-start;
			justify-content: space-between; }

		&__item:not(:last-child) {
			margin-bottom: 18px; } } }


/*	 320	*/
@media (max-width: $ms) {
	.section-about {
		&__inner {
			max-width: 280px; } } }
