.section-assurance {
   &__content {
      padding-top: 5.71rem;
      padding-bottom: 5.64rem; }

   &__inner {
      max-width: 966px;
      margin: 0 auto; }

   &__column {
      display: flex;
      flex-direction: column; }

   &__description {
      margin-top: 5rem;

      &-paragraph {
         @include lightItalic;
         @include lh;
         font-size: 16px;
         color: #000;
         text-align: center; } }

   &__page {
      margin-top: 4.36rem;

      &-subtitle {
         text-align: center;

         & > h3 {
            @include lh; } }

      &-items {
         margin: 35px auto 0 auto;
         margin-bottom: 0;
         max-width: 900px;
         display: flex;
         justify-content: space-between; }

      &-item {
         display: flex;
         flex-direction: column;
         align-items: center;

         &-image {
            max-width: 140px;
            max-height: 180px; }

         &-paragraph {
            margin-top: 12px;
            @include lh;
            @include regular;
            font-size: 14px;
            color: #000; } } } }


@media (max-width: 1440px) {
   .section-assurance {
      &__inner {
         max-width: 860px; }

      &__page {
         &-items {
            margin: 30px auto 0 auto; } } } }


/*  1200  */
@media (max-width: $xl) {
   .section-assurance {
      &__inner {
         max-width: 810px; }

      &__page {
          margin-top: 5.5rem;
          &-item {
            &-paragraph {
               font-size: 12px; } } } } }

/*  992  */
@media (max-width: $lg) {
   .section-assurance {
      &__inner {
         max-width: 690px; }

      &__description {
         &-paragraph {
            font-size: 14px; } }

      &__page {
          margin-top: 5.5rem;
          &-item {
            &-paragraph {
               font-size: 12px; } } } } }


/*  768  */
@media (max-width: $md) {
   .section-assurance {
      &__inner {
         max-width: 560px; }

      &__description {
         &-paragraph {
            font-size: 13px; } }

      &__page {
         margin-top: 9rem;

         &-items {
            flex-wrap: wrap;
            margin-top: 0; }

         &-item {
            flex: 1 1 50%;
            margin-top: 30px;
            &-paragraph {
               font-size: 12px; } } } } }

/*  660  */
@media (max-width: 660px) {
   .section-assurance {
      &__inner {
         max-width: 500px; }

      &__description {
         &-paragraph {
            line-height: 24px; } } } }

/*  576  */
@media (max-width: $sm) {
   .section-assurance {
      &__inner {
         max-width: 420px; }

      &__page {
         margin-top: 10rem;

         &-item {
            &-paragraph {
               font-size: 13px; } } } } }

/*  460  */
@media (max-width: $ml) {
   .section-assurance {
      &__inner {
         max-width: 320px; }

      &__page {
         margin-top: 10rem;

         &-item {
            &-paragraph {
               font-size: 12px; } } } } }

/*  375  */
@media (max-width: $mm) {
   .section-assurance {
      &__inner {
         max-width: 300px; }

      &__page {
         &-items {
            flex-direction: column;
            align-items: center; }

         &-item:not(:first-child) {
            margin-top: 20px; } } } }

/*  320  */
@media (max-width: $ms) {
   .section-assurance {
      &__inner {
         max-width: 286px; } } }
